import React from 'react'
import { Route, Link } from 'react-router-dom'
import Home from '../home'
import About from '../about'
import Gallery from '../gallery'
import Paintings from '../paintings'
import Photos from '../photos'
// eslint-disable-next-line
import { MenuItem, Nav, Navbar, NavDropdown, NavItem } from 'react-bootstrap';
import './index.css';
const App = () => (
  <div >
    <header>
      <Navbar  >
        <div className="container">
          <Navbar.Header>
            <Navbar.Brand>
              <Link to="/"><i className="fa fa-vk"></i></Link>
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>
          <Navbar.Collapse>
            <Nav>
              <NavItem  componentClass="span" eventKey={1} >
                <Link activeClassName='active' to="/paintings">Paintings</Link>
              </NavItem>
              <NavItem  componentClass="span" eventKey={2} href="#">
                <Link activeClassName='active' to="/photos">Photos</Link>
              </NavItem>
              <NavItem  componentClass="span" eventKey={3} href="#">
                <Link activeClassName='active' to="/gallery">Gallery</Link>
              </NavItem>
              <NavItem  componentClass="span" eventKey={4} href="#">
                <Link activeClassName='active' to="/about">Upload</Link>
              </NavItem>
              {/* <NavDropdown eventKey={3} title="Dropdown" id="basic-nav-dropdown">
              <MenuItem eventKey={3.1}>Action</MenuItem>
              <MenuItem eventKey={3.2}>Another action</MenuItem>
              <MenuItem eventKey={3.3}>Something else here</MenuItem>
              <MenuItem />
              <MenuItem eventKey={3.3}>Separated link</MenuItem>
            </NavDropdown> */}
            </Nav>

          </Navbar.Collapse>
        </div>
      </Navbar>
    </header>

    <main>
      <div className="container">

        <Route exact path="/" component={Home} />
        <Route exact path="/paintings" component={Paintings} />
        <Route exact path="/photos" component={Photos} />
        <Route exact path="/gallery" component={Gallery} />
        <Route exact path="/about" component={About} />
      </div>
    </main>
  </div>
)

export default App




